<template>
  <v-layout wrap>
    <v-flex sm12 lg3 class="pa-3 mb-3 feature-pane">
      <v-text-field label="Title" placeholder="Event Title" v-model="details.title"></v-text-field>
      <v-menu ref="startMenu" v-model="startMenu" :close-on-content-click="false" :nudge-right="40" :return-value.sync="details.date" transition="scale-transition" min-width="290px" lazy offset-y full-width>
        <template v-slot:activator="{ on }">
          <v-text-field v-model="details.date" label="Date" prepend-icon="event" readonly v-on="on"></v-text-field>
        </template>
        <v-date-picker v-model="details.date" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn flat color="primary" @click="startMenu = false">
            Cancel
          </v-btn>
          <v-btn flat color="primary" @click="$refs.startMenu.save(details.date)">
            OK
          </v-btn>
        </v-date-picker>
      </v-menu>
      <v-select v-model="eventOps" :items="eventOptions" label="Type" prepend-icon="event_note"></v-select>
      <v-text-field label="Duration (hrs)" placeholder="Duration" prepend-icon="schedule"></v-text-field>
      <v-select v-model="intensity" :items="intensityOptions" label="Energy Intensity" prepend-icon="settings"></v-select>
      <v-flex class="text-xs-right"><v-btn flat color="primary" @click="() => saveEvent()" >SAVE</v-btn></v-flex>
    
    </v-flex>
      
      
    <v-flex sm12 lg9 class="pl-3">
      <v-card-title class="display-2">
          <v-flex>
            <p class="text-xs-center">Kendeda Event Calendar</p>
            <p class="text-xs-center display-1">{{calendarMonth + ' ' + calendarYear}}</p>
          </v-flex>
      </v-card-title>
      <v-sheet height="500">
        <v-calendar type="month" ref="calendar" v-model="calendarStart" :now="today" :value="today" color="primary">
          <template v-slot:day="{ date }">
            <template v-for="event in eventsMap[date]">
              <v-menu :key="event.title" v-model="event.open" full-width offset-x>
                <template v-slot:activator="{ on }">
                  <div v-if="!event.time" v-ripple class="my-event" v-on="off" v-html="event.title"></div>
                </template>

              </v-menu>
            </template>
          </template>
        </v-calendar>
      </v-sheet>
      <v-card-actions>
          <v-flex xs6 class="text-xs-left">
            <v-btn color="primary" @click="$refs.calendar.prev(),getCalendarMonth(),getCalendarYear()"><v-icon>chevron_left</v-icon>Prev</v-btn>
          </v-flex>
          <v-flex xs6 class="text-xs-right">
            <v-btn color="primary" @click="$refs.calendar.next(),getCalendarMonth(),getCalendarYear()">Next<v-icon>chevron_right</v-icon></v-btn>
          </v-flex>
      </v-card-actions>
    </v-flex>
  </v-layout>
</template>

<script>
  export default {
    data: () => ({
      today: new Date().getFullYear()+'-'+(new Date().getMonth()+1)+'-'+new Date().getDate(),
      calendarStart: '',
      calendarMonth: new Date().toLocaleString('en-us', { month: 'long' }),
      calendarYear: new Date().getFullYear(),
      startMenu: false,
      details:{
          title: '',
          date: new Date().getFullYear()+'-'+(new Date().getMonth()+1)+'-'+new Date().getDate(),
      },
      eventOps: 'primary',
      eventOptions: [
          {text: 'Event', value: 'newEvent', class: 'yellow'},
          {text: 'Power Outage', value: 'powerOut'},
          {text: 'Water Outage', value: 'waterOut'}
      ],
      intensity: 'primary',
      intensityOptions: ['low','medium','high'],
      events: [
        {
          title: 'Energy Outage',
          date: '2019-05-30'
        },
        {
          title: 'Event',
          date: '2019-05-07'
        }
      ]
    }),
    computed: {
      // convert the list of events into a map of lists keyed by date
      eventsMap () {
        const map = {}
        this.events.forEach(e => (map[e.date] = map[e.date] || []).push(e))
        return map
      }
    },
    methods: {
      open (event) {
        alert(event.title)
      },
      getCalendarYear(){
        let dateObject = new Date();
        if(this.calendarStart !== ''){
          dateObject = new Date(this.calendarStart.replace("-","/"));
        }
        this.calendarYear = dateObject.getFullYear();
      },
      getCalendarMonth(){
        let dateObject = new Date();
        if(this.calendarStart !== ''){
         dateObject = new Date(this.calendarStart.replace("-","/"));
        }
        this.calendarMonth = dateObject.toLocaleString('en-us',{ month: 'long' });
      },
      saveEvent(){
          var title = this.details.title
          var date = this.details.date
          this.events.push(this.details)
          //this.events.title = this.events.title.push(title)
          //this.events.date = this.events.title.push(date)
          //var element = {}
          //var old = this.events
          //element.title = title;
          //element.date = date;
          //var newele = _.merge(old,element)
          //this.events = this.events.push(element)
          console.log(this.events)

          //this.events[2etitle = title
          //this.events[2].date = date
          //console.log(this.events)
      }
    }
  }
</script>

